<template>
  <div class='Scheduling'>
    <div>
      <CommonHeader @getUserMes='getUserMes'></CommonHeader>
    </div>
    <!--tab导航部分-->
    <div class="fix">
      <Set></Set>
    </div>
    <!--内容区域-->
    <div class='Scheduling-content'>
      <!--头部-->
      <div class='content-header'>
        <div class='c-header'>
          <div class='c-h-Text'>预约排班</div>
          <span class='border-cover'></span>
        </div>
      </div>
      <!--头部-->
      <!--查询条件模块-->
      <div class='content-condition'>
        <div class='condition-top'>
          <div class='row'>
            <div class='colorClass col-md-6'>
              <span class='s1'></span>
              <span class='s2'>已过期排班</span>
              <span class='s3'></span>
              <span class='s4'>有排班</span>
              <span class='s5'></span>
              <span class='s6'>有预约排班</span>
            </div>
            <div class='dateCarousel col-md-6'>
              <span></span>
              <div class='mainDateCarousel'>
                <span class='arrow1' @click='arrowLeft'></span>
                <ul class='clearfix' :class='activeObject'>
                  <li class='pull-left' v-for='item in dayArr'>{{item.start}}至{{item.end}}</li>
                </ul>
                <span class='arrow2' @click='arrowRight'></span>
              </div>
            </div>
          </div>
        </div>
        <div class='condition-bottom'>
          <!--<span style='line-height:34px'>查询条件：</span>-->
          <select v-if='hospitalShow' style='margin-right:15px;' v-model='condition2.hospitalId'>
            <option value="">全部医院</option>
            <option v-for='item in hospitalList' :value="item.id">{{item.name}}</option>
          </select>
          <input type="text" v-model="condition2.search" placeholder='专家姓名/手机号' class="form-control P-t-inputBox3" style='width:200px; margin:0;display: inline'>
          <button style='margin-left:10px;' type="button" class='btn btn-primary' @click='search'>查询</button>
        </div>
      </div>
      <!--查询条件模块-->
      <!--表格-->
      <div class='content-table'>
        <table class="table table-bordered">
          <thead>
            <tr>
              <th rowspan="2">姓名</th>
              <th rowspan="2">手机号</th>
              <td v-for='(item,index) in dateHeader' :class='{todayBack:index==0&&todayBackground}'>{{index==0&&todayBackground==true?'今天':item}}</td>
            </tr>
            <tr>
              <td v-for='item in weekArr'>{{item}}</td>
            </tr>
          </thead>
          <tbody>
            <tr v-for='handleSingle in handleList'>
              <td>{{handleSingle.userName}}</td>
              <td>{{handleSingle.euMobile}}</td>
              <td v-for='(item,index) in handleSingle.schedules' class='choose-td' @click='alreadyOrder(item,index)' :class='{scheduling2:item.arr,scheduling3:item.status,scheduling1:item.arr&&scheduling1}'></td>
            </tr>
            <tr v-if='noMesShow2'>
              <td colspan="16">
                暂无数据
              </td>
            </tr>
          </tbody>
        </table>

      </div>
      <!--表格-->
      <!--小弹框-->
      <div class='little-model'>
        <el-dialog :title="userName+'  '+date+'  的预约排班'" :visible.sync="dialogVisible" size="tiny">
          <table class='table table-bordered'>
            <thead>
              <tr>
                <th></th>
                <th>前30分钟</th>
                <th>后30分钟</th>
              </tr>
            </thead>

            <tbody>
              <tr v-for='item in SchedulingArr'>
                <td>{{item.time1}}</td>
                <!--<td style='cursor:pointer' @click='item.status1=!item.status1'>-->
                <td @click='firstHalf(item)' :class="{'pointer':item.usedNum1<=0&&!item.nowDate1,'disabled':item.usedNum1>0||item.nowDate1}">
                  <el-checkbox v-model="item.status1" :disabled='item.usedNum1>0?true:false || item.nowDate1'></el-checkbox>
                  <span style='color:#20a0ff;margin-left:8px;'>({{item.usedNum1}}/2已预约)</span>
                </td>
                <!--<td style='cursor:pointer' @click='item.status2=!item.status2'>-->
                <td @click='afterHalf(item)' :class="{'pointer':item.usedNum2<=0&&!item.nowDate2,'disabled':item.usedNum2>0||item.nowDate2}">
                  <el-checkbox v-model="item.status2" :disabled='item.usedNum2>0?true:false || item.nowDate2'></el-checkbox>
                  <span style='color:#20a0ff;margin-left:8px;'>({{item.usedNum2}}/2已预约)</span>
                </td>
              </tr>
            </tbody>
          </table>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="confirmScheduling">确 定</el-button>
          </span>
        </el-dialog>
      </div>
      <!--小弹框-->
      <!--翻页组件-->
      <flip @customEvent='customEvent' :Body='schedulingBody' :Condition='condition'></flip>
      <!--翻页组件-->
    </div>
  </div>
</template>
<script>  
//header
import CommonHeader from '../public/header.vue';
//menu
import Set from '../public/set.vue';
import moment from 'moment';
import flip from '../public/flip.vue'
export default {
  data() {
    return {
      count: 2,
      activeObject: {
        active1: false,
        active2: true,
        active3: false,
        active4: false,
        active5: false,
      },//class对象
      dateArr: ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'],//用来做匹配
      weekArr: [],//表格星期列表
      whichWeek: new Date(),//当前view所在是周几
      dayArr: [],//轮播的列表
      dateHeader: [],//表格日期的渲染
      dialogVisible: false,//弹出框
      SchedulingArr: [
        { time1: '09:00', time2: '09:30', status1: false, status2: false, usedNum1: 0, usedNum2: 0, nowDate1: false, nowDate2: false },
        { time1: '10:00', time2: '10:30', status1: false, status2: false, usedNum1: 0, usedNum2: 0, nowDate1: false, nowDate2: false },
        { time1: '11:00', time2: '11:30', status1: false, status2: false, usedNum1: 0, usedNum2: 0, nowDate1: false, nowDate2: false },
        { time1: '12:00', time2: '12:30', status1: false, status2: false, usedNum1: 0, usedNum2: 0, nowDate1: false, nowDate2: false },
        { time1: '13:00', time2: '13:30', status1: false, status2: false, usedNum1: 0, usedNum2: 0, nowDate1: false, nowDate2: false },
        { time1: '14:00', time2: '14:30', status1: false, status2: false, usedNum1: 0, usedNum2: 0, nowDate1: false, nowDate2: false },
        { time1: '15:00', time2: '15:30', status1: false, status2: false, usedNum1: 0, usedNum2: 0, nowDate1: false, nowDate2: false },
        { time1: '16:00', time2: '16:30', status1: false, status2: false, usedNum1: 0, usedNum2: 0, nowDate1: false, nowDate2: false },
        { time1: '17:00', time2: '17:30', status1: false, status2: false, usedNum1: 0, usedNum2: 0, nowDate1: false, nowDate2: false },
        { time1: '18:00', time2: '18:30', status1: false, status2: false, usedNum1: 0, usedNum2: 0, nowDate1: false, nowDate2: false },
        { time1: '19:00', time2: '19:30', status1: false, status2: false, usedNum1: 0, usedNum2: 0, nowDate1: false, nowDate2: false },
        { time1: '20:00', time2: '20:30', status1: false, status2: false, usedNum1: 0, usedNum2: 0, nowDate1: false, nowDate2: false }
      ],
      condition: {
        employeeId: '',
        search: '',
        dir: 'asc',
        hospitalId: '',
        startDate: '',
        endDate: '',
        pageNumber: 1,
        pageSize: 10
      },//查询条件
      condition2: {
        employeeId: '',
        search: '',
        dir: 'asc',
        hospitalId: '',
        startDate: '',
        endDate: '',
        pageNumber: 1,
        pageSize: 10
      },
      todayBackground: false,//今天的表格背景色
      schedulingList: [],//排班列表 
      handleList: [],//处理完的排班数组
      regularDate: [],//正规日期数组 
      scheduling1: false,
      userName: '',
      date: '',//弹出框
      hospitalList: [],//医院列表
      schedulingBody: {},
      SchedulingObj: {//排班条件
        "date": "2017-08-19T02:34:50.999Z",
        "schedules": [],
        "usersId": 0
      },
      hospitalShow: true,
      noMesShow2: false,//是否有数据
      IndependentSearch: ''//独立的搜索条件，解决双向数据绑定的问题
    }
  },
  created() {
    // 获取星期的列表
    this.getTableWeek()
    // 获取轮播的列表
    this.getCarouselList();
    // 表格日期的渲染
    this.getTableDate();
    // 获取列表
    this.getSchedulingList();
    // 获取所有医院
    this.searchHospital()
  },
  methods: {
    //  轮播图向左
    arrowLeft() {
      this.count--
      if (this.count <= -1) {
        this.count = 0
      }
      this.arrowBoolean();
    },
    arrowRight() {
      this.count++;
      if (this.count >= 6) {
        this.count = 5
      }
      this.arrowBoolean()
    },
    // 箭头判断函数
    arrowBoolean() {
      var index = 1000 * 60 * 60 * 24 * 29
      if (this.count == 0) {
        this.whichWeek = new Date(new Date() - 1000 * 60 * 60 * 24 * 28)
        this.getTableWeek()
        this.getTableDate()
        this.activeObject = {
          active1: false,
          active2: false,
          active3: false,
          active4: false,
          active5: false,
        }
      } else if (this.count == 1) {
        this.whichWeek = new Date(new Date() - 1000 * 60 * 60 * 24 * 14)
        this.getTableWeek()
        this.getTableDate()
        this.activeObject = {
          active1: true,
          active2: false,
          active3: false,
          active4: false,
          active5: false,
        }
      } else if (this.count == 2) {
        this.whichWeek = new Date()
        this.getTableWeek()
        this.getTableDate()
        this.activeObject = {
          active1: false,
          active2: true,
          active3: false,
          active4: false,
          active5: false,
        }
      } else if (this.count == 3) {
        this.whichWeek = new Date(new Date().getTime() + 1000 * 60 * 60 * 24 * 14)
        this.getTableWeek()
        this.getTableDate()
        this.activeObject = {
          active1: false,
          active2: false,
          active3: true,
          active4: false,
          active5: false,
        }
      } else if (this.count == 4) {
        this.whichWeek = new Date(new Date().getTime() + 1000 * 60 * 60 * 24 * 28)
        this.getTableWeek()
        this.getTableDate()
        this.activeObject = {
          active1: false,
          active2: false,
          active3: false,
          active4: true,
          active5: false,
        }
      } else if (this.count == 5) {
        this.whichWeek = new Date(new Date().getTime() + 1000 * 60 * 60 * 24 * 42)
        this.getTableWeek()
        this.getTableDate()
        this.activeObject = {
          active1: false,
          active2: false,
          active3: false,
          active4: false,
          active5: true,
        }
      }
      this.getSchedulingList()
    },
    // 表格星期的渲染
    getTableWeek() {
      var which = this.whichWeek.getDay();
      this.weekArr = []
      for (var i = 0; i < 14; i++) {
        this.weekArr.push(this.dateArr[which]);
        which++
        if (which > 6) {
          which = 0;
        }
      }
    },
    // 表格日期的渲染
    getTableDate() {
      var which = this.whichWeek
      var date = new Date()
      this.dateHeader = [];
      this.regularDate = []
      if (this.whichWeek.getDate() == date.getDate() && this.whichWeek.getMonth() == date.getMonth()) {
        this.todayBackground = true
      } else {
        this.todayBackground = false
      }
      for (var i = 0; i < 14; i++) {
        var index = which.getTime() + i * (1000 * 60 * 60 * 24)
        var day = new Date(index)
        this.dateHeader.push(day.getMonth() + 1 + '/' + day.getDate())
        this.regularDate.push(moment(day).format('YYYY-MM-DD'));
      }
    },
    // 获取轮播列表
    getCarouselList() {
      var index = 1000 * 60 * 60 * 24 * 14 * 2
      var $index = 1000 * 60 * 60 * 24 * 13
      var nowDay = new Date() - index;
      var newArr = []
      for (var i = 0; i < 12; i++) {
        newArr.push(moment(nowDay).format('YYYY-MM-DD'))
        if (i % 2 != 0) {
          nowDay = nowDay + $index / 13
        } else {
          nowDay = nowDay + $index
        }
      }
      for (var i = 0; i < newArr.length; i++) {
        this.dayArr.push({ start: newArr[i], end: newArr[i + 1] })
        i++
      }
    },
    // 选择排班
    alreadyOrder(obj, index) {
      if (this.scheduling1) {
        return
      }
      this.userName = obj.userName;
      this.date = moment(obj.date).format('YYYY-MM-DD');
      this.SchedulingObj = {//排班条件
        "date": this.date,
        "schedules": [],
        "usersId": obj.usersId
      }
      this.SchedulingArr = [
        { time1: '09:00', time2: '09:30', time3: '10:00', status1: false, status2: false, usedNum1: 0, usedNum2: 0, date: this.date, usersId: obj.usersId, nowDate1: false, nowDate2: false },
        { time1: '10:00', time2: '10:30', time3: '11:00', status1: false, status2: false, usedNum1: 0, usedNum2: 0, date: this.date, usersId: obj.usersId, nowDate1: false, nowDate2: false },
        { time1: '11:00', time2: '11:30', time3: '12:00', status1: false, status2: false, usedNum1: 0, usedNum2: 0, date: this.date, usersId: obj.usersId, nowDate1: false, nowDate2: false },
        { time1: '12:00', time2: '12:30', time3: '13:00', status1: false, status2: false, usedNum1: 0, usedNum2: 0, date: this.date, usersId: obj.usersId, nowDate1: false, nowDate2: false },
        { time1: '13:00', time2: '13:30', time3: '14:00', status1: false, status2: false, usedNum1: 0, usedNum2: 0, date: this.date, usersId: obj.usersId, nowDate1: false, nowDate2: false },
        { time1: '14:00', time2: '14:30', time3: '15:00', status1: false, status2: false, usedNum1: 0, usedNum2: 0, date: this.date, usersId: obj.usersId, nowDate1: false, nowDate2: false },
        { time1: '15:00', time2: '15:30', time3: '16:00', status1: false, status2: false, usedNum1: 0, usedNum2: 0, date: this.date, usersId: obj.usersId, nowDate1: false, nowDate2: false },
        { time1: '16:00', time2: '16:30', time3: '17:00', status1: false, status2: false, usedNum1: 0, usedNum2: 0, date: this.date, usersId: obj.usersId, nowDate1: false, nowDate2: false },
        { time1: '17:00', time2: '17:30', time3: '18:00', status1: false, status2: false, usedNum1: 0, usedNum2: 0, date: this.date, usersId: obj.usersId, nowDate1: false, nowDate2: false },
        { time1: '18:00', time2: '18:30', time3: '19:00', status1: false, status2: false, usedNum1: 0, usedNum2: 0, date: this.date, usersId: obj.usersId, nowDate1: false, nowDate2: false },
        { time1: '19:00', time2: '19:30', time3: '20:00', status1: false, status2: false, usedNum1: 0, usedNum2: 0, date: this.date, usersId: obj.usersId, nowDate1: false, nowDate2: false },
        { time1: '20:00', time2: '20:30', time3: '21:00', status1: false, status2: false, usedNum1: 0, usedNum2: 0, date: this.date, usersId: obj.usersId, nowDate1: false, nowDate2: false }
      ]
      // 预约数据的回显
      if (obj.status) {
        for (var i = 0; i < this.SchedulingArr.length; i++) {
          var flag = true;
          for (var j = 0; j < obj.arr.length; j++) {
            var hour = new Date(obj.arr[j].startTime).getHours()
            var min = new Date(obj.arr[j].startTime).getMinutes()
            if (min < 10) {
              min = '0' + min
            }
            if (hour < 10) {
              hour = '0' + hour
            }
            var time = hour + ':' + min
            if (time == this.SchedulingArr[i].time1) {
              this.SchedulingArr[i].usedNum1 = obj.arr[j].usedNum
            }
            if (time == this.SchedulingArr[i].time2) {
              this.SchedulingArr[i].usedNum2 = obj.arr[j].usedNum
            }
          }
        }
      }
      // 排班数据的回显
      if (obj.arr) {
        for (var i = 0; i < this.SchedulingArr.length; i++) {
          var flag = true;
          for (var j = 0; j < obj.arr.length; j++) {
            var hour = new Date(obj.arr[j].startTime).getHours()
            var min = new Date(obj.arr[j].startTime).getMinutes()
            if (min < 10) {
              min = '0' + min
            }
            if (hour < 10) {
              hour = '0' + hour
            }
            var time = hour + ':' + min
            if (time == this.SchedulingArr[i].time1) {
              this.SchedulingArr[i].status1 = true
            }
            if (time == this.SchedulingArr[i].time2) {
              this.SchedulingArr[i].status2 = true
            }
          }
        }
      }
      // 禁止选择过期时间
      if (index == 0 && this.todayBackground) {
        var disabledHour = new Date().getHours()
        var disabledMin = new Date().getMinutes()
        for (var i = 0; i < this.SchedulingArr.length; i++) {
          var time1 = this.SchedulingArr[i].time1.slice(0, 2);
          if (time1.slice(0, 1) == 0) {
            time1 = 9
          }
          if (disabledHour == time1) {
            this.SchedulingArr[i].nowDate1 = true
            if (disabledMin >= 30) {
              this.SchedulingArr[i].nowDate2 = true
            }
          } else if (disabledHour > time1) {
            this.SchedulingArr[i].nowDate1 = true
            this.SchedulingArr[i].nowDate2 = true
          }

        }
      }
      this.dialogVisible = !this.dialogVisible
    },
    // 获取排班列表
    getSchedulingList() {
      var url = '/api/organization/assistSchedules/list'
      this.Authorization = localStorage.getItem('access_token');
      this.condition.startDate = moment(this.whichWeek).format('YYYY-MM-DD') + ' 00:00:00'
      this.condition.endDate = moment(this.whichWeek.getTime() + 1000 * 60 * 60 * 24 * 13).format('YYYY-MM-DD') + ' 23:59:59'
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + this.Authorization
        },
        params: this.condition
      }).then((res) => {
        this.schedulingBody = res.body
        this.totalAll = res.body.lastPage
        var handleList = res.body.list
        if (handleList.length == 0) {
          this.noMesShow2 = true
        } else if (handleList.length > 0) {
          this.noMesShow2 = false
        }
        for (var i = 0; i < handleList.length; i++) {
          // 无时补齐
          if (handleList[i].schedules.length == 0) {
            for (var j = 0; j < this.regularDate.length; j++) {
              handleList[i].schedules.push({
                date: this.regularDate[j],
                usersId: handleList[i].usersId,
                status: false,
                userName: handleList[i].userName
              })
            }
          } else {
            // 同时归一
            var schedules1 = []
            for (var j = 0; j < handleList[i].schedules.length; j++) {
              var flag = true
              for (var k = 0; k < schedules1.length; k++) {
                if (handleList[i].schedules[j].date == schedules1[k].date) {
                  schedules1[k].arr.push(handleList[i].schedules[j])
                  flag = false;
                  break;
                }
              }
              if (flag) {
                schedules1.push({ date: handleList[i].schedules[j].date, usersId: handleList[i].schedules[j].usersId, arr: [handleList[i].schedules[j]], userName: handleList[i].userName })
              }
            }
            // 状态补齐
            for (var j = 0; j < schedules1.length; j++) {
              for (var k = 0; k < schedules1[j].arr.length; k++) {
                if (schedules1[j].arr[k].usedNum > 0) {
                  schedules1[j].status = true
                  break;
                } else {
                  schedules1[j].status = false
                }
              }
            }
            handleList[i].schedules = schedules1
            // 缺则补齐
            var schedules2 = []
            for (var j = 0; j < this.regularDate.length; j++) {
              var flag = true;
              for (var k = 0; k < handleList[i].schedules.length; k++) {
                if (moment(handleList[i].schedules[k].date).format('YYYY-MM-DD') == this.regularDate[j]) {
                  schedules2.push(handleList[i].schedules[k])
                  flag = false;
                  break;
                }
              }
              if (flag) {
                schedules2.push({
                  usersId: handleList[i].usersId,
                  date: this.regularDate[j],
                  status: false,
                  userName: handleList[i].userName
                })
              }
            }
            handleList[i].schedules = schedules2
          }
        }
        this.handleList = handleList
        // 是否过期
        var date = new Date()
        if (this.whichWeek.getMonth() == date.getMonth()) {
          if (this.whichWeek.getDate() < date.getDate()) {
            this.scheduling1 = true
          } else {
            this.scheduling1 = false
          }
        } else {
          if (this.whichWeek.getMonth() < date.getMonth()) {
            this.scheduling1 = true
          } else {
            this.scheduling1 = false
          }

        }
        // console.log('处理完毕', this.handleList)
      })
    },
    // 查询
    search() {
      //解决同象地址一致问题，解决双绑
      for(var key in this.condition2){
         this.condition[key]=this.condition2[key]
      }
      this.condition.pageNumber = 1
      this.getSchedulingList();
    },
    // 查询所有医院
    searchHospital() {
      var url = '/api/organization/hospital/list';
      this.Authorization = localStorage.getItem('access_token');
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + this.Authorization
        },
        params: {
          type:2,
          status:0
        }
      }).then(res => {
        this.hospitalList = res.body
      })
    },
    // 排班提交
    confirmScheduling() {
      for (var i = 0; i < this.SchedulingArr.length; i++) {
        // 前
        if (this.SchedulingArr[i].status1) {
          this.SchedulingObj.schedules.push({
            "date": this.SchedulingArr[i].date,
            "endTime": moment(this.SchedulingArr[i].date + ' ' + this.SchedulingArr[i].time2).format(),
            "startTime": moment(this.SchedulingArr[i].date + ' ' + this.SchedulingArr[i].time1).format(),
            "totalNum": 2,
            "usedNum": this.SchedulingArr[i].usedNum1,
            "usersId": this.SchedulingArr[i].usersId
          })
        }
        // 后
        if (this.SchedulingArr[i].status2) {
          this.SchedulingObj.schedules.push({
            "date": this.SchedulingArr[i].date,
            "endTime": moment(this.SchedulingArr[i].date + ' ' + this.SchedulingArr[i].time3).format(),
            "startTime": moment(this.SchedulingArr[i].date + ' ' + this.SchedulingArr[i].time2).format(),
            "totalNum": 2,
            "usedNum": this.SchedulingArr[i].usedNum2,
            "usersId": this.SchedulingArr[i].usersId
          })
        }
      }
      var url = '/api/organization/assistSchedules/list';
      this.Authorization = localStorage.getItem('access_token');
      var SchedulingObj = JSON.stringify(this.SchedulingObj)
      this.$http.post(url, SchedulingObj, {
        headers: {
          Authorization: 'bearer ' + this.Authorization
        },
        emulateJSON: true
      }).then(res => {
        this.getSchedulingList()
        this.dialogVisible = false;
      }, err => {
        if (err.body.code == 200 && err.body.status == 500) {
          this.dialogVisible = false;
          this.$message({
            message: err.body.message,
            type: 'warning'
          });
        }
      })
    },
    // 获取头部传来的用户信息
    getUserMes(body) {
      if (body.roleId == 1316 || body.roleId == 1320) {
        this.hospitalShow = true
      } else {
        this.hospitalShow = false
      }
    },
    // 排班的点击区域
    firstHalf(item) {
      if (item.usedNum1 > 0 || item.nowDate1) {
        return
      } else {
        item.status1 = !item.status1
      }
    },
    afterHalf(item) {
      if (item.usedNum2 > 0 || item.nowDate2) {
        return
      } else {
        item.status2 = !item.status2
      }
    },
    customEvent(condition) {
      this.condition = condition;
      this.getSchedulingList()
    }

  },
  components: {
    CommonHeader: CommonHeader,
    Set: Set,
    flip: flip
  }
}
</script>
<style scoped>
/*大局*/

.Scheduling-content {
  margin: 50px 15px 0px 195px;
  min-width: 960px;
}








/*头部*/

.Scheduling-content .content-header .c-header {
  padding: 8px 0px;
  font-size: 14px;
  height: 50px;
  background: white;
  position: relative;
  border-bottom: 1px solid #ddd;
}

.c-h-Text {
  padding: 8px 8px 8px 8px;
}

.c-header .border-cover {
  display: block;
  width: 1px;
  height: 14px;
  background: #6886d4;
  position: absolute;
  left: 1px;
  top: 17px;
}








/*查询条件模块*/

.Scheduling-content .content-condition {
  margin-top: 30px;
}

.Scheduling-content .content-condition .condition-top {
  /*padding-bottom: 30px;*/
}

.Scheduling-content .content-condition .colorClass .s1 {
  display: inline-block;
  width: 40px;
  height: 40px;
  background: #CCCCCC;
  margin-right: 3px;
  margin-left: 13px;
}

.Scheduling-content .content-condition .colorClass .s3 {
  display: inline-block;
  width: 40px;
  height: 40px;
  background: #009900;
  margin-right: 3px;
  margin-left: 3px;
}

.Scheduling-content .content-condition .colorClass .s5 {
  display: inline-block;
  width: 40px;
  height: 40px;
  background: #0099FF;
  margin-right: 3px;
  margin-left: 3px;
}








/*小轮播图样式*/

.dateCarousel {
  position: relative;
  width: 240px;
  padding: 0px;
}

.mainDateCarousel {
  font-size: 20px;
  font-weight: 700;
  text-align: center;
  width: 240px;
  height: 50px;
  line-height: 50px;
  overflow: hidden;
}

.mainDateCarousel ul {
  width: 1440px;
  transition: transform 1s;
}

.mainDateCarousel ul.active1 {
  transform: translate(-240px, 0px)
}

.mainDateCarousel ul.active2 {
  transform: translate(-480px, 0px)
}

.mainDateCarousel ul.active3 {
  transform: translate(-720px, 0px)
}

.mainDateCarousel ul.active4 {
  transform: translate(-960px, 0px)
}

.mainDateCarousel ul.active5 {
  transform: translate(-1200px, 0px)
}

.mainDateCarousel ul li {
  width: 240px;
  padding: 0px 5px;
}

.mainDateCarousel .arrow1 {
  position: absolute;
  left: -40px;
  top: 0px;
  border-left: solid 20px transparent;
  border-right: solid 20px #ccc;
  border-top: solid 20px transparent;
  border-bottom: solid 20px transparent;
  border-radius: 5px;
  cursor: pointer;
}

.mainDateCarousel .arrow2 {
  position: absolute;
  right: -40px;
  top: 0px;
  border-right: solid 20px transparent;
  border-left: solid 20px #ccc;
  border-top: solid 20px transparent;
  border-bottom: solid 20px transparent;
  border-radius: 5px;
  cursor: pointer;
}








/*查询下半部分样式*/

.condition-bottom {
  padding: 30px;
}

.condition-bottom select {
  height: 34px;
  width: 200px;
  border: solid 1px #ccc;
  border-radius: 4px;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
}








/*表格样式*/

.content-table .table tr {
  text-align: center;
}

.content-table .table td {
  padding: 4px 0px;
  line-height: 39px;
}

.content-table .table tbody .choose-td {
  cursor: pointer;
}

.content-table .table th {
  text-align: center;
  border-bottom: none;
  vertical-align: middle;
}

.todayBack {
  background: #eef1f6;
}

.scheduling2 {
  background: #009900;
}

.scheduling3 {
  background: #0099FF;
}

.scheduling1 {
  background: #CCCCCC;
}








/*小弹框样式*/

.little-model table td,
.little-model table th {
  text-align: center;
}






/*按钮样式*/

.pointer {
  cursor: pointer;
}

.disabled {
  cursor: not-allowed;
}
</style>


